.main {
    text-align: center;
}

.header {
    margin-top: 10rem;
}

.main h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 3.5rem;
    letter-spacing: -0.04em;
}

.main p {
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 23px;
    color: var(--color-subtext);
    margin-top: 1rem;
}

.download-section {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(655px, auto));
    justify-content: center;
    margin-top: 4rem;
}

.download-container {
    width: 650px;
    background: rgb(9, 9, 9);
    height: 280px;
    border: 1px solid white;
    margin: 40px;
    padding: 45px 40px;
    border-radius: 8px;
}

.box-title,
.box-title-counter {
    display: flex;
    justify-content: left;
}

.box-title h1 {
    font-family: var(--font-family);
    font-weight: 600;
    font-size: 2rem;
    letter-spacing: -0.04em;
}

.box-content {
    text-align: left;
}

.box-content p {
    font-family: var(--font-family);
    margin-top: 0.5rem;
    font-weight: 200;
    font-size: 23px;
}

.download-buttons {
    margin-top: 1.5rem;
    text-align: left;
}

.button {
    background-color: #e6e6e6;
    padding: 11px 30px;
    color: rgb(0, 0, 0);
    cursor: pointer;
    font-family: var(--font-family);
    font-weight: 200;
    font-size: 22px;
    border-radius: 8px;
}

.changelog-bookmark {
    margin-left: 22rem;
}

