.nav {
justify-content: space-between;
position: fixed;
z-index: 100;
width: 100%;
left: 0;
background-color: #050505bb;
backdrop-filter: blur(0.5rem);
padding-top: 0.7rem;
align-items: center;
}


.logo {
    flex: 1;
    gap: 1rem;
    margin:  0 12rem ;
    margin-bottom: 0.8rem;
    display: flex;
    justify-content: space-between;
    align-items: center;

}

.logo img {
    margin-top: 1px;
    margin-bottom: 1px;
    margin-left: 1rem;
    width: auto;
    height: 50px;
    image-rendering: -webkit-optimize-contrast;
}

.info {
    color: white;
}

.navbar-links_container {
    display: flex;
    flex-direction: row;
}

.navbar-links_container p {
    color: white;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 18px;
    line-height: 40px;
    text-transform: uppercase;
    margin: 0 2rem;
    cursor: pointer;
}

@media screen and (max-width: 1650px) {
    .logo {
        margin: 0 6rem;
    }
}

@media screen and (max-width: 1400px) {
    .logo {
        margin: 0 2rem;
    }
}