.crawenOS__features-heading::before {
  height: 3rem;
  content: '';
  display: block;
}

.crawenOS__features {
  width: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}

.crawenOS__features-heading {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: left;
  margin-right: 5rem;
}

.crawenOS__features-heading h1 {
  font-size: 34px;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
}

.crawenOS__features-heading p {
  color: var(--color-subtext);
  font-size: 16px;
  line-height: 30px;
  font-weight: 500;
  font-family: var(--font-family);

  margin-top: 2rem;
}

.crawenOS__features-container::before {
  height: 9rem;
  content: '';
  display: block;
}

.crawenOS__features-container {
  flex: 1.5;
  display: flex;
  gap: 1rem;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

@media screen and (max-width: 990px) {
  .crawenOS__features {
    flex-direction: column;
  }

  .crawenOS__features-heading {
    margin: 0 0 2rem 0;
  }
}
