.crawenOS__features-container__feature {
  width: 100%;
  display: flex;
  justify-content: space-between align-items flex-start;
  flex-direction: row;
  margin: 2rem;
}

.crawenOS__features-container__feature-title {
  flex: 1;
  max-width: 200px;
  margin-right: auto;
}

.crawenOS__features-container__feature-title h1 {
  font-family: var(--font-family);
  font-weight: 600;
  font-size: 20px;
  line-height: 20px;
  letter-spacing: -0.04em;
  color: #f2f2f2;
}

.crawenOS__features-container__feature-title div {
  width: 35px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  margin-bottom: 0.25rem;
}

.crawenOS__features-container_feature-text {
  flex: 2;
  max-width: 380px;
  display: flex;
}

.crawenOS__features-container_feature-text p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 14px;
  line-height: 24px;
  color: var(--color-text);
}

@media screen and (max-width: 550px) {
  .crawenOS__features-container__feature-titleh1 {
    font-size: 14px;
    line-height: 22px;
  }

  .crawenOS__features-container_feature-text p {
    font-size: 12px;
    line-height: 20px;
  }

  .crawenOS__features-container_feature {
    margin: 1rem 0;
  }
}
