.crawenOS__header {
  display: flex;
  padding: 3rem;
}

.crawenOS__header:before {
  height: 50px;
  content: '';
  display: block;
}

.crawenOS__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.crawenOS__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 4rem;
  line-height: 5rem;
  letter-spacing: -0.04em;
}

.crawenOS__header-content p {
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 20px;
  line-height: 25px;
  color: var(--color-text);
  margin-top: 2rem;
}

.crawenOS__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 6rem;
}

.crawenOS__header-image img {
  border-radius: 1.5%;
  height: 630px;
  width: auto;
}

@media screen and (max-width: 1650px) {
  .crawenOS__header {
    flex-direction: column;
  }

  .crawenOS__header-image img {
    height: 900px;
  }

  .crawenOS__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 1350px) {
  .crawenOS__header {
    flex-direction: column;
  }

  .crawenOS__header-image img {
    height: 700px;
  }

  .crawenOS__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 1050px) {
  .crawenOS__header {
    flex-direction: column;
  }

  .crawenOS__header-image img {
    height: 450px;
  }

  .crawenOS__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 700px) {
  .crawenOS__header-content h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .crawenOS__header-image img {
    height: 350px;
  }

  .crawenOS__header-content p {
    font-size: 16px;
    line-height: 24px;
  }
  .crawenOS__header-image {
    margin-top: 2rem;
  }
}

@media screen and (max-width: 490px) {
  .crawenOS__header-content h1 {
    font-size: 36px;
    line-height: 48px;
  }

  .crawenOS__header-content p {
    font-size: 14px;
    line-height: 24px;
  }

  .crawenOS__header-image img {
    height: 230px;
  }

  .crawenOS__header-content__input input,
  .crawenOS__header-content__input button {
    font-size: 12px;
    line-height: 16px;
  }
}
