

.crawenOS__whatcrawenos {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  background: var(--color-footer);
  background: rgb(5, 5, 5);
}

.crawenOS__whatcrawenos-feature {
  display: flex;
}

/* Customizing feature component as per needs */
.crawenOS__whatcrawenos-feature .crawenOS__features-container__feature {
  margin: 0;
}

/* Customizing feature component as per needs */
.crawenOS__whatcrawenos-feature .crawenOS__features-container_feature-text {
  max-width: 700px;
}

.crawenOS__whatcrawenos-heading {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  margin: 3rem 0 3rem;
}

.crawenOS__whatcrawenos-heading h1 {
  font-size: 34px;
  text-align: left;
  line-height: 45px;
  font-weight: 800;
  font-family: var(--font-family);
  max-width: 510px;
}

.crawenOS__whatcrawenos-heading p {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  text-align: left;
  margin-right: 10rem;
  line-height: 30px;
  color: var(--color-subtext);
  cursor: pointer;
}

.crawenOS__whatcrawenos-container {
  display: flex;
  flex-wrap: wrap;
  text-align: left;
  flex-direction: row;

  margin-top: 2rem;
}

/* Customizing feature component as per needs */
.crawenOS__whatcrawenos-container .crawenOS__features-container__feature {
  flex: 1;
  margin: 1rem;
  min-width: 210px;
  display: unset;
  flex-direction: column;
}

/* Customizing feature component as per needs */
.crawenOS__whatcrawenos-container .crawenOS__features-container_feature-text {
  margin-top: 0.7rem;
}

@media screen and (max-width: 850px) {
  .crawenOS__whatcrawenos-heading {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0rem;
  }

  .crawenOS__whatcrawenos-heading p {
    margin-top: 1rem;
  }
}

@media screen and (max-width: 700px) {
  /* Customizing feature component as per needs */
  .crawenOS__whatcrawenos-feature .crawenOS__features-container__feature {
    flex-direction: column;
  }

  /* Customizing feature component as per needs */
  .crawenOS__whatcrawenos-feature .crawenOS__features-container_feature-text {
    margin-top: 0.5rem;
  }

  .crawenOS__whatcrawenos-heading h1 {
    font-size: 28px;
    line-height: 40px;
  }

  .crawenOS__whatcrawenos:before {
    height: 280px;
  }
}

@media screen and (max-width: 350px) {
  /* Customizing feature component as per needs */
  .crawenOS__whatcrawenos-container .crawenOS__features-container__feature {
    margin: 1rem 0;
    min-width: 100%;
  }
}
