.crawenOS__navbar {
  justify-content: space-between;
  position: fixed;
  z-index: 100;
  width: 100%;
  left: 0;
  background-color: #050505bb;
  backdrop-filter: blur(0.5rem);
  padding-top: 0.7rem;
  align-items: center;
}

.crawenOS__navbar-links {
  flex: 1;
  gap: 1rem;
  margin: 0 12rem;
  margin-bottom: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.crawenOS__navbar-links_logo {
  margin-right: auto;
}

.crawenOS__navbar-links_logo img {
  margin-top: 1px;
  margin-bottom: 1px;
  margin-left: 1rem;
  width: auto;
  height: 50px;
  image-rendering: -webkit-optimize-contrast;
}

.crawenOS__navbar-links_container {
  display: flex;
  flex-direction: row;
}

.crawenos_navbar-downlaod {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.crawenOS__navbar-menu_container p,
.crawenOS__navbar-links_container p,
.crawenOS_navbar-downlaod p {
  color: white;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 18px;
  line-height: 40px;
  text-transform: capitalize;
  margin: 0 2rem;
  cursor: pointer;
}

.glow-on-hover {
  width: 220px;
  height: 48px;
  border: none;
  outline: none;
  color: #fff;
  border: #fbfbfb solid 1px;
  font-family: var(--font-family);
  font-weight: 300;
  font-size: 18px;
  background: #212121;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
}

.glow-on-hover:before {
  content: '';
  background: linear-gradient(
    45deg,
    rgb(253, 10, 149),
    rgb(254, 0, 110),
    rgb(230, 75, 253),
    rgb(200, 0, 219),
    rgb(230, 75, 253),
    rgb(254, 0, 110),
    rgb(253, 10, 149)
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(10px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}

.glow-on-hover:hover:before {
  opacity: 1;
}

.glow-on-hover:after {
  z-index: -1;
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: #111;
  left: 0;
  top: 0;
  border-radius: 10px;
}

.crawenOS__navbar-menu {
  display: none;
  position: relative;
}

.crawenOS__navbar-menu svg {
  cursor: pointer;
}

.crawenOS__navbar-menu_container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  background: #050505;
  padding: 1rem;
  position: absolute;
  top: 40px;
  right: -13px;
  padding-top: 3rem;
  margin-top: 1.4rem;
  overflow-y: hidden;
  border-radius: 5px;
  min-width: 150px;
}

.crawenOS__navbar-menu_container p {
  margin: 4rem 0;
}

.crawenOS__navbar-menu_container-links-downlaod {
  display: none;
}


@keyframes glowing {
  0% {
    background-position: 0 0;
  }

  50% {
    background-position: 400% 0;
  }

  100% {
    background-position: 0 0;
  }
}

@media screen and (max-width: 1650px) {
  .crawenOS__navbar-links {
    margin: 0 2rem;
  }
}

@media screen and (max-width: 1050px) {
  .crawenOS__navbar-links_container {
    display: none;
  }

  .crawenOS__navbar-links {
    margin: 0 2rem;
  }

  .crawenOS__navbar-download {
    display: none;
  }

  .crawenOS__navbar-menu {
    display: flex;
  }
}

@media screen and (max-width: 700px) {
  .crawenOS__navbar {
    padding: 0rem 1rem;
    background-color: #050505b4;
  }
}

@media screen and (max-width: 550px) {
  .crawenOS__navbar {
    padding: 0px;
    background-color: #05050592;
  }

  .crawenOS__navbar-links {
    margin: 0 1.5rem;
  }

  .crawenOS__navbar-links {
    margin: 0 1rem;
  }

  .crawenOS__navbar-download {
    display: none;
  }

  .crawenOS__navbar-download-mobile {
    display: fixed;
  }

  .crawenOS__navbar-menu_container {
    top: 20px;
  }

  .crawenOS__navbar-links_logo img {
    margin-top: 8px;
    margin-left: 0rem;
    width: 50%;
    height: 50%;
    image-rendering: -webkit-optimize-contrast;
  }

  .crawenOS__navbar-menu_container-links-downlaod {
    display: none;
  }
}
